.main-container {
  max-width: 1440px;
  width: 100%;
  height: 100vh;
  margin: auto;
  display: flex;
  flex-direction: row;
}

.image-section {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.image-section .image {
  margin: 10%;
}

.content-section {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  margin: 5% 15% 5% 5%;
}
.content-section .title {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: rgb(18, 18, 18);
}
.content-section .description {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: rgb(18, 18, 18);
  margin-bottom: 50px;
}
.content-section .button {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 16px 40px;
  width: 128px;
  height: 48px;
  background-color: rgb(228, 193, 174);
  border-radius: 32px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  flex: none;
  order: 0;
  flex-grow: 0;
  color: rgb(255, 255, 255);
}